import Link from 'next/link';
import Image from 'next/image';

export default function Page() {
  return (
    <div className="flex flex-col min-h-screen bg-[var(--background)] text-[var(--foreground)] relative overflow-x-hidden">
      {/* 祥云装饰 */}
      <div className="chinese-cloud" style={{top: 24, left: 32}}>
        <svg viewBox="0 0 80 40" fill="none" xmlns="http://www.w3.org/2000/svg">
          <path d="M10 30 Q 20 10, 40 20 T 70 25" stroke="#e7b85a" strokeWidth="3" fill="none"/>
        </svg>
      </div>
      <main className="flex-grow container mx-auto px-4 py-12 z-10">
        <div className="relative mb-12">
          <Link href="/" className="absolute -top-8 left-0 flex items-center gap-2 text-[var(--border)] hover:text-[var(--accent)] transition-colors font-bold text-xl" style={{fontFamily: 'var(--font-chinese)'}}>
            <span className="text-2xl">←</span>
            <span>返回首页</span>
          </Link>
        </div>
        <section className="max-w-3xl mx-auto mb-10 chinese-border bg-black/70 p-8">
          <h2 className="text-2xl font-bold mb-4 text-[var(--border)]" style={{fontFamily: 'var(--font-chinese)'}}>案例说明：HTML 基础标签演示</h2>
          <ul className="list-disc list-inside text-[var(--accent)] space-y-2 mb-2" style={{fontFamily: 'var(--font-chinese)'}}>
            <li>本案例演示了 HTML 的常用标签，包括标题、段落、超链接、列表、图片、表格和表单。</li>
            <li>所有元素均可交互体验，表单可直接提交。</li>
          </ul>
        </section>
        <section className="max-w-3xl mx-auto chinese-border bg-black/60 p-8">
          <h1 className="text-3xl font-bold mb-6 text-[var(--border)]" style={{fontFamily: 'var(--font-chinese)'}}>一级标题</h1>
          <div className="prose max-w-none prose-p:text-[var(--accent)] prose-a:text-[var(--border)] hover:prose-a:text-[var(--foreground)] prose-li:text-[var(--foreground)] prose-table:border-[var(--border)] prose-th:bg-black/80 prose-th:text-[var(--border)] prose-td:border-[var(--border)] prose-img:my-2 prose-img:border prose-img:border-[var(--border)] prose-img:shadow-lg prose-img:rounded-none" style={{fontFamily: 'var(--font-chinese)'}}>
            <p>
              段落标记，a是行内元素，效果是这样的，
              <a href="https://yangzh.cn" target="_blank" rel="noopener noreferrer">这是一个超链接地址。</a>
            </p>
            <ol>
              <li>这是列表</li>
              <li>这是列表</li>
              <li>这是列表</li>
              <li>这是列表</li>
            </ol>
            <p className="flex gap-4 flex-wrap items-center">
              <Image src="/practice/images/th.jpg" alt="图片替代说明文字" width={200} height={120} />
              <Image src="https://images.unsplash.com/photo-1506744038136-46273834b3fb" alt="未来风格示例图片" width={200} height={120} />
            </p>
            <table className="w-full border border-[var(--border)] text-[var(--foreground)]">
              <tbody>
                <tr>
                  <td className="border border-[var(--border)] p-2">单元格1</td>
                  <td className="border border-[var(--border)] p-2">单元格1</td>
                  <td className="border border-[var(--border)] p-2">单元格1</td>
                </tr>
                <tr>
                  <td className="border border-[var(--border)] p-2">单元格1</td>
                  <td className="border border-[var(--border)] p-2">单元格1</td>
                  <td className="border border-[var(--border)] p-2">单元格1</td>
                </tr>
                <tr>
                  <td className="border border-[var(--border)] p-2">单元格1</td>
                  <td className="border border-[var(--border)] p-2">单元格1</td>
                  <td className="border border-[var(--border)] p-2">单元格1</td>
                </tr>
              </tbody>
            </table>
            <form action="https://search.douban.com/book/subject_search" method="get" className="mt-4 flex gap-2 items-center">
              <input type="text" name="search_text" className="px-3 py-2 bg-black border border-[var(--border)] text-[var(--foreground)] focus:outline-none focus:ring-2 focus:ring-[var(--border)]" placeholder="豆瓣图书搜索..." style={{fontFamily: 'var(--font-chinese)'}} />
              <input type="submit" value="搜索" className="px-4 py-2 border border-[var(--border)] bg-black text-[var(--border)] font-semibold hover:bg-[var(--border)] hover:text-black transition-colors cursor-pointer" style={{fontFamily: 'var(--font-chinese)'}} />
            </form>
          </div>
        </section>
      </main>
      {/* 古建筑底部装饰 */}
      <div className="chinese-building">
        <svg viewBox="0 0 220 90" fill="none" xmlns="http://www.w3.org/2000/svg">
          <rect x="30" y="60" width="160" height="20" fill="#bfa76a" stroke="#e7b85a" strokeWidth="3"/>
          <path d="M30 60 Q 110 10, 190 60" stroke="#e7b85a" strokeWidth="5" fill="none"/>
          <rect x="80" y="40" width="60" height="20" fill="#bfa76a" stroke="#e7b85a" strokeWidth="2"/>
          <path d="M80 40 Q 110 20, 140 40" stroke="#e7b85a" strokeWidth="3" fill="none"/>
        </svg>
      </div>
    </div>
  );
} 